* { 
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
  font-family: 'Fira Sans', 'sans-serif'; 
}
.App { 
  min-height: 100vh; 
  position:relative; 
  background-color: #313131;
}
.camera { 
  position: relative; 

}

.result { 
  position:fixed; 
  top: 0; 
  left: 100%; 
  width: 100%;
  height: 100%; 
  display:flex;
  align-items: center; 
  background-color: rgb(0, 0, 0, 0.9);
  transition: 0.4s; 

}

.result.hasPhoto { 
  left: 0; 
}

video { 
  width: 100%;
  max-width: 100%; 
  height: auto; 
}

button { 
  position: absolute; 
  bottom: 20px; 
  left: 20px; 

  appearance: none; 
  border: none; 
  padding: 8px 16px; 
  background-image: rgb(185, 222, 240); 
  background-position: 0%; 
  background-size: 200%; 
  color: rgb(185, 222, 240);

}

canvas { 
  width: 100%; 
  height: 100%; 
}